<template>
	<div id="sign">
		<van-nav-bar>
			<div slot="title">
				签到
			</div>
			<van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
		</van-nav-bar>

		<div class="sign">
			<p>{{signd}}</p>
			<p class="line"></p>
			<p>连续签到{{howmany}}天</p>
		</div>
		<div class="nowtime">当前时间：{{nowtime}}</div>
		<div class="record">注：连续签到七天可得2元。</div>
	</div>
</template>

<script>
    export default {
        name: "sign",
		data(){
			return{
				nowtime:'',
				signd:'',
				howmany:'',
			}
		},
		created (){
			this.time()
			this.signIn()
		},
		methods: {
			goBack () {
				console.log('goback')
				this.$router.go(-1)
			},
			time(){
				var date = new Date();
				var year = date.getFullYear();
				var month = date.getMonth() + 1;
				var day = date.getDate();
				if (month < 10) {
					month = "0" + month;
				}
				if (day < 10) {
					day = "0" + day;
				}
				this.nowtime = year + "年" + month + "月" + day + "日";
				// console.log(this.nowtime,"当前时间");
			},
			//点击签到
			signIn(){
				this.$POST('/user/sign').then(res =>{
					console.log(res);
					if(res.code===200){
						this.signd = "已签到";
						this.howmany = res.data;
						this.$toast(res.msg);
					}else{
						this.signd = "签到";
						this.$toast(res.msg);
					}
				}).catch(res=>{
					this.signd = "签到";
					console.log(res);
				})
			}
		},
    }
</script>

<style scoped>
	.sign{
		border: 1px solid #1989FA;width: 120px;height: 120px;border-radius: 50%;
		position: absolute;top: 30%;left: 50%; transform: translate(-50%, -50%);
	}
	.sign p{color: #44BB00;}
	.line{border-top: 1px solid #EEEEEE;}
	.nowtime{
		padding-top: 2rem;
	}
	.record{
		margin-top: 180px;padding:0 1rem;color: #E12532;
	}
</style>




